@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300,300i,700&display=swap);
.homepage{display:flex;flex-direction:column;align-items:center;padding:1em 1.5em}

.menu-item{min-width:30%;height:240px;flex:1 1 auto;display:flex;align-items:center;justify-content:center;border:1px solid black;margin:0 7.5px 15px;font-family:'Open Sans Condensed', sans-serif;overflow:hidden;-webkit-transition:all .3s ease;transition:all .3s ease}.menu-item:hover{cursor:pointer}.menu-item:hover .background-image{-webkit-transform:scale(1.1);transform:scale(1.1);-webkit-transition:all 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);transition:all 6s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.menu-item:hover .content{opacity:1}.menu-item.large{height:40vh}.menu-item:first-child{margin-right:7.5px}.menu-item:last-child{margin-left:7.5px}.menu-item .background-image{width:100%;height:100%;background-position:center;background-size:cover}.menu-item .content{text-transform:uppercase;height:90px;padding:0 25px;display:flex;flex-direction:column;align-items:center;justify-content:center;border:1px solid black;background:rgba(255,255,255,0.63);opacity:0.7;position:absolute;-webkit-transition:all .3s ease;transition:all .3s ease;border-radius:2px}.menu-item .content .title{font-weight:bold;margin-bottom:6px;font-size:22px;color:#4a4a4a}.menu-item .content .subtitle{font-weight:lighter;font-size:16px}

.directory-menu{width:100%;display:flex;flex-wrap:wrap;justify-content:space-between}

